.signn {
  position: absolute;
  display: flex;
  z-index: -1;
  justify-content: center;

  align-items: center;
  width: 50%;
  height: 50%;
  background-image: radial-gradient(
    ellipse 50% 35% at 50% 50%,
    #6b1839,
    transparent
  );
  transform: translate(-50%, -50%);
  letter-spacing: 2;
  left: 50%;
  top: 50%;
  font-family: "Clip";
  text-transform: uppercase;
  font-size: 6em;
  color: #ffe6ff;
  text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #ff65bd,
    -0.2rem 0.1rem 1rem #ff65bd, 0.2rem 0.1rem 1rem #ff65bd,
    0 -0.5rem 2rem #ff2483, 0 0.5rem 3rem #ff2483;
  animation: shine 2s forwards, flicker 3s infinite;
}

@keyframes blink {
  0%,
  22%,
  36%,
  75% {
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #ff65bd,
      -0.2rem 0.1rem 1rem #ff65bd, 0.2rem 0.1rem 1rem #ff65bd,
      0 -0.5rem 2rem #ff2483, 0 0.5rem 3rem #ff2483;
  }
  28%,
  33% {
    color: #7a0404;
    text-shadow: none;
  }
  82%,
  97% {
    color: #720707;
    text-shadow: none;
  }
}

.flicker {
  animation: shine 2s forwards, blink 3s 2s infinite;
}

.fast-flicker {
  animation: shine 2s forwards, blink 10s 1s infinite;
}

@keyframes shine {
  0% {
    color: #6d0000;
    text-shadow: none;
  }
  100% {
    color: #770707;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #971d2d,
      -0.2rem 0.1rem 1rem #bb1e26, 0.2rem 0.1rem 1rem #9c1d1d,
      0 -0.5rem 2rem #a32323, 0 0.5rem 3rem #961b1b;
  }
}

@keyframes flicker {
  from {
    opacity: 1;
  }

  4% {
