.main {
  height: 100vh;
  width: 100%;
  /* background-image: url("./sky.jpg"); */
  z-index: -1;
  background-position: center;
  position: relative;
  overflow-x: hidden;
}

.road {
  height: 200px;
  width: 500%;
  display: block;
  background-image: url("./road.jpg");
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  animation: road 5s linear infinite;
}
@keyframes road {
  100% {
    transform: translateX(-3400px);
  }
}

.tree {
  height: 250px;
  width: 500%;
  background-image: url("./city.png");
  position: absolute;
  bottom: 200px;
  left: 0;
  right: 0;
  display: block;
  z-index: 1;
  background-repeat: repeat-x;
  animation: tree 20s linear infinite;
}
@keyframes tree {
  100% {
    transform: translateX(-1400px);
  }
}
.car {
  width: 400px;
  left: 50%;
  bottom: 100px;
  transform: translateX(-50%);
  position: absolute;
  z-index: 2;
}
.car img {
  width: 100%;
  animation: car 1s linear infinite;
}
@keyframes car {
  100% {
    transform: translateY(-1px);
  }
  50% {
    transform: translateY(1px);
  }
  0% {
    transform: translateY(-1px);
  }
}
.wheel {
  left: 50%;
  bottom: 178px;
  transform: translateX(-50%);
  position: absolute;
  z-index: 2;
}
.wheel img {
  width: 72px;
  height: 72px;
  animation: wheel 1s linear infinite;
}
@keyframes wheel {
  100% {
    transform: rotate(360deg);
  }
}
.back-wheel {
  left: -165px;
  position: absolute;
}
.front-wheel {
  left: 80px;
  position: absolute;
}

.blinking {
  animation: blinkingText 2s infinite;
}
@keyframes blinkingText {
  0% {
    color: rgb(2, 58, 83);
  }
  49% {
    color: rgb(2, 58, 83);
  }
  60% {
    color: transparent;
  }
  99% {
    color: transparent;
  }
  100% {
    color: rgb(2, 58, 83);
  }
}
